:root {
  --background-color: #292945;
  --card-background-color: #38385b;
  --text-color: #ffffff;
  --heading-color: #f8f9fa;
  --preview-border-color: #55557f;
  --footer-background-color: #38385b;
  --footer-text-color: #ffffff;
  --color-box-font-size: 0.8rem;
  --color-box-border-radius: 8px;
  --preview-border-radius: 8px;
  --preview-max-height: 300px;
  --gap-between-colors: 15px;
  --btn-primary-bg: #007bff;
  --btn-primary-hover-bg: #0056b3;
  --card-border-radius: 12px;
  --card-padding: 20px;
  --file-upload-border-radius: 12px;
  --file-upload-bg: #f1f1f1;
  --file-upload-hover-bg: #e0e0e0;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  margin: 0;
  font-family: "Poppins", sans-serif;
}

.container {
  max-width: 900px;
  margin: auto;
}

h1 {
  color: var(--heading-color);
}

.card {
  background-color: var(--card-background-color);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.preview-image {
  max-height: var(--preview-max-height);
  object-fit: cover;
  border: 2px solid var(--preview-border-color);
  border-radius: var(--preview-border-radius);
  margin-bottom: 15px;
}

.footer {
  background-color: var(--footer-background-color);
  padding: 20px;
  border-radius: var(--preview-border-radius);
  text-align: center;
  color: var(--footer-text-color);
}

.color-palette {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: var(--gap-between-colors);
  margin-top: 15px;
}

.color-box {
  width: 100px;
  height: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--footer-text-color);
  font-size: var(--color-box-font-size);
  border-radius: var(--color-box-border-radius);
  text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.7);
  background-color: rgba(255, 255, 255, 0.1);
  padding: 10px;
  transition: transform 0.2s ease;
}

.color-box:hover {
  transform: translateY(-5px);
}

.color-box span {
  font-size: 0.75rem;
  margin-top: 5px;
}

.btn-primary {
  background-color: var(--btn-primary-bg);
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
}

.btn-primary:hover {
  background-color: var(--btn-primary-hover-bg);
}

.btn-success {
  background-color: #28a745;
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
}

.btn-success:hover {
  background-color: #218838;
}

input[type="file"] {
  display: none;
}

.file-upload-box {
  border-radius: 12px;
  border: 2px dashed #ccc;
  padding: 40px;
  background-color: #f9f9f9;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  background: transparent;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.file-upload-box:hover {
  transform: scale(1.03);
}

.file-upload-box.drag-over {
  background-color: #e0f7fa;
  border-color: #007bff;
}

.upload-text {
  color: wheat;
  font-size: 1.2rem;
  font-weight: 500;
}

.upload-text:hover {
  color: wheat;
}

.upload-icon {
  font-size: 2rem;
  color: wheat;
  margin-bottom: 10px;
}

.title-gradient {
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  background: linear-gradient(to right, #FF5733, #8E2F97);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.btn-gradient {
  background: linear-gradient(to right, #292945, #8e2f97);
  border: none;
  color: white;
  font-size: 15px;
  font-weight: bold;
  padding: 10px 20px;
  border-radius: 30px;
  text-transform: uppercase;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.btn-gradient:hover {
  background: linear-gradient(to right, #8e2f97, #292945);
  transform: translateY(-4px);
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.3);
}

.btn-gradient:focus {
  outline: none;
}